import { useEffect, useState, ReactNode } from 'react';
import { useRouter } from 'next/router';
import { decodeToken, getStoredToken, getStoredUser, clearAuth, isTokenValid } from '../utils/auth';
import { setAuthToken } from '../services/api';

interface ProtectedRouteProps {
  children: ReactNode;
  allowedRoles: string[];
}

export const ProtectedRoute = ({ children, allowedRoles }: ProtectedRouteProps) => {
  const router = useRouter();
  const [isAuthorized, setIsAuthorized] = useState<boolean | null>(null);

  useEffect(() => {
    const validateAndAuthorize = async () => {
      const token = getStoredToken();
      const storedUser = getStoredUser();

      // No token or user info
      if (!token || !storedUser) {
        router.push('/login');
        return;
      }

      // Token invalid or expired
      if (!isTokenValid(token)) {
        clearAuth();
        router.push('/login');
        return;
      }

      // Decode token to get current role
      const decoded = decodeToken(token);
      if (!decoded) {
        clearAuth();
        router.push('/login');
        return;
      }

      // Role mismatch
      if (!allowedRoles.includes(decoded.role)) {
        // Redirect to correct dashboard based on role
        if (decoded.role === 'admin') {
          router.push('/admin/dashboard');
        } else if (decoded.role === 'vendor') {
          router.push('/vendor/dashboard');
        } else {
          router.push('/customer/dashboard');
        }
        return;
      }

      // All checks passed - ensure token is set in API
      setAuthToken(token);
      setIsAuthorized(true);
    };

    validateAndAuthorize();
  }, [router, allowedRoles]);

  // Show nothing while validating
  if (isAuthorized === null) {
    return <div style={{ padding: '20px', textAlign: 'center' }}>Loading...</div>;
  }

  // Only render if authorized
  if (!isAuthorized) {
    return null;
  }

  return <>{children}</>;
};

