export interface DecodedToken {
  id: string;
  role: string;
  iat: number;
  exp: number;
}

export const decodeToken = (token: string): DecodedToken | null => {
  try {
    const parts = token.split('.');
    if (parts.length !== 3) return null;

    const decoded = JSON.parse(atob(parts[1]));
    return decoded;
  } catch (error) {
    return null;
  }
};

export const isTokenValid = (token: string): boolean => {
  const decoded = decodeToken(token);
  if (!decoded) return false;

  const now = Math.floor(Date.now() / 1000);
  return decoded.exp > now;
};

export const getStoredToken = (): string | null => {
  if (typeof window === 'undefined') return null;
  return localStorage.getItem('cc_token');
};

export const getStoredUser = (): { id: string; role: string } | null => {
  if (typeof window === 'undefined') return null;
  const user = localStorage.getItem('cc_user');
  if (!user) return null;

  try {
    const parsed = JSON.parse(user);
    return { id: parsed.id, role: parsed.role };
  } catch {
    return null;
  }
};

export const clearAuth = (): void => {
  if (typeof window === 'undefined') return;
  localStorage.removeItem('cc_token');
  localStorage.removeItem('cc_user');
  localStorage.removeItem('cc_vendorId');
  localStorage.removeItem('cc_customerId');
};
